import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/main/Login.vue'
import Register from '../views/main/register.vue'
import Userinfo from '../views/user/userinfo.vue'
import Article from '../views/writeArticle.vue'

Vue.use(VueRouter)

const routes = [{
		path: '/',
		name: 'Home',
		component: Home
	},
	{
		path: '/person',
		name: 'Person',
		// route level code-splitting
		// this generates a separate chunk (about.[hash].js) for this route
		// which is lazy-loaded when the route is visited.
		component: () =>
			import( /* webpackChunkName: "about" */ '../views/person.vue')
	},
	{
		path: '/login',
		name: 'Login',
		component: Login
	},
	{
		path: '/register',
		name: 'Register',
		component: Register
	},
	{
		path: '/user/userinfo',
		name: 'Userinfo',
		component: Userinfo
	},
	{
		path: '/article',
		name: 'Article',
		component: Article
	}
]

const router = new VueRouter({
	mode: "history",
	routes
})

export default router